/*  */

.course-display {
  /* display: flex; */
  margin: 5px;
  width: auto;
  border: 1px transparent;
  border-radius: 10px;
  /* padding-top: 5px; */
  padding-bottom: 10px;
  background-color: rgb(230, 225, 225);
}

.course-display img {
  border-radius: 10px;
}

.course-display button {
  display: block;
  cursor: pointer;
  padding: 2%;
  border: transparent;
  background-color: rgb(57, 178, 226);
  border-radius: 20px;
  margin-left: 35%;
}

.category {
  background-color: aqua;
  margin: 5px;
  padding: 2px;
  border: 1px;
  border-radius: 5px;
}

.type {
  background-color: rgb(200, 195, 195);
  padding: 2px;
  border: 1px;
  border-radius: 5px;
}

.description {
  display: inline-block;
  width: 200px;
  height: 80px;
  padding-left: 10px;
}
