.checkboxes {
  width: 13px;
  height: 13px;
  cursor: pointer;
  border-radius: 20%;
  color: black;
  border: 1px solid white;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /* outline: 2px solid white; */
  position: relative;
}

.checkboxes:checked {
  background: #2196f3;
}

.filter-names {
  color: white;
}

.filter-labels {
  cursor: pointer;
  color: white;
}

.filter-types {
  color: rgb(0, 145, 199);
